import { HomeComponent } from "./pages/home/home.component";
import { terms } from "./static_pages/terms.component";

export const appRoutes = [
  {
    path: "",
    redirectTo: "home",
    pathMatch: "full"
  },
  {
    path: "home",
    component: HomeComponent
  },
  // {
  //   path: "terms",
  //   component: terms
  // },
  {
    path: "search",
    loadChildren: "./pages/others/others.module#OthersModule"
  }
];



