import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";

import { AppComponent } from "./app.component";
import { Topnavbar } from "./components/topnavbar/topnavbar.component";
import { Navigation } from "./components/navigation/navigation.component";
import { footerRent } from "./components/footer-rent/footer-rent.component";
import { footerCopyright } from "./components/footer-copyright/footer-copyright.component";
import { topHeader } from "./components/top-header/top-header.component";
import { RouterModule } from "@angular/router";
import { appRoutes } from "./app.routes";
import { HomeComponent } from "./pages/home/home.component";
import { HomepageHeroComponent } from "./homepage-hero/homepage-hero.component";
// import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
// import { OwlDateTimeModule, OwlNativeDateTimeModule } from "ng-pick-datetime";

@NgModule({
  declarations: [
    AppComponent,
    Navigation,
    Topnavbar,
    HomeComponent,
    footerRent,
    footerCopyright,
    topHeader,
    HomepageHeroComponent
  ],
  imports: [BrowserModule, FormsModule, HttpModule, RouterModule.forRoot(appRoutes)],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {}



